@import "ScssHelpers/helpers_all";

.team_card {
}

.team_card_image {

	@include bp($point_4, min) {
		margin-bottom: 4.2rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 3rem;
	}
}

.team_card_image__in {
	position: relative;
	display: block;
	width: 100%;
	height: 0;
	padding-bottom: 100%;
}

.team_card_image__img {
	@extend %cover_img;

	position: absolute;
	left: 0;
	top: 0;
}

.team_card_heading {

	@include bp($point_4, min) {
		min-height: 7rem;
	}

	&:not(:last-child) {

		@include bp($point_4, min) {
			margin-bottom: 2.6rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}
}

.team_card_name {
	@extend %h3;

	letter-spacing: -.04em;

	&:not(:last-child) {

		@include bp($point_4, min) {
			margin-bottom: 1rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: .8rem;
		}
	}
}

.team_card_position {
	color: var(--gray_color);
	text-transform: uppercase;
	font-weight: 500;
	letter-spacing: -.04em;

	@include bp($point_4, min) {
		font-size: 1.8rem;
	}
	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

.team_card_descr {
	@extend %base_text_2;

	letter-spacing: -.04em;
}
