@import "ScssHelpers/helpers_all";

.team {
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding-top: 16rem;
		padding-bottom: 16rem;
	}

	@include bp($point_3) {
		padding-top: 6.4rem;
		padding-bottom: 6.4rem;
	}
}

.team_in {
	position: relative;

	&:not(:last-child) {

		@include bp($point_2, min) {
			margin-bottom: 17.6rem;
		}
		@include bp($point_3, $point_4) {
			margin-bottom: 12.6rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 6rem;
		}
	}
}

.team_heading {
	@include bp($point_4, min) {
		@include flex_block(row, nowrap, center);
	}
}

.team_icon {

	@include bp($point_2, min) {
		width: 11.2rem;
		height: 8rem;
		margin-right: 4.8rem;
	}
	@include bp($point_4, min) {
		padding-top: .2rem;
	}
	@include bp($point_3) {
		width: 8.4rem;
		height: 6rem;
	}
	@include bp($point_3, $point_4) {
		margin-right: 4rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 2.8rem;
	}
}

.team_title {
	@extend %accent;

	letter-spacing: -.02em;

	color: var(--primary_color);

	@include bp($point_2, min) {
		margin-right: 9.4rem;
		width: 36.2rem;
	}
	@include bp($point_3, $point_4) {
		font-size: 2.6rem;
		width: 30rem;
		margin-right: 4.4rem;
	}
	@include bp($point_4 - 1) {
		width: 66%;
		margin-bottom: 3.4rem;
		font-size: 2.5rem;
		line-height: 1.17;
	}
}

.team_descr {
	@extend %base_text_2;

	font-weight: 500;
	letter-spacing: -.02em;

	@include bp($point_4, min) {
		width: 44.2rem;
	}

	@include bp($point_4 - 1) {
		width: 85%;
	}

	a {
		color: var(--blue_color_2);
		transition: color .3s ease-in-out;

		&:hover,
		&:focus {
			text-decoration: none;
			color: var(--orange_color);
		}
	}
}

.team_list {
	@include bp($point_2, min) {
		margin: 0 -5.2rem -11.6rem 0;
	}
	@include bp($point_3, $point_4) {
		margin: 0 -3rem -6rem 0;
	}
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
	}
}

.team_item {

	@include bp($point_2, min) {
		padding: 0 5.2rem 11.6rem 0;
		width: 25%;
	}
	@include bp($point_3, $point_4) {
		padding: 0 3rem 6rem 0;
		width: 33.333%;
	}
	@include bp($point_4 - 1) {
		max-width: 36rem;
		margin: 0 auto;

		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}


